import React from 'react';
import { AppBar, Toolbar, IconButton, Tooltip, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { IoSettingsOutline } from "react-icons/io5";

const Header: React.FC = () => {
    const navigate = useNavigate();
    const { t } = useTranslation();

    const handleGoToSetting = () => {
        console.log('Go to setting Clicked!');

        navigate('/setting');
    };

    return (
        <>
            <AppBar sx={{ backgroundColor: '#107C41', position: 'static' }}>
                <Toolbar sx={{ display: "flex", alignItems: "center", justifyContent: "space-between", gap: 2 }}>
                    <Typography sx={{ fontWeight: 600, fontSize: "18px" }}>
                        {t('header.title')}
                    </Typography>

                    <Tooltip title="Setting" placement="left-end">
                        <IconButton
                            aria-label="Setting"
                            size="small"
                            sx={{ fontSize: "20px" }}
                            onClick={handleGoToSetting}
                        >
                            <IoSettingsOutline style={{ color: "#fff" }} />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
            </AppBar>
        </>
    );
};

export default Header;