import React, { useEffect, useState } from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Loader from '../../../loader/Loader';

const Dashboard: React.FC = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [functions, setFunctions] = useState<Array<any>>([]);
    const [selectedFunction, setSelectedFunction] = useState("");


    const handleChange = (event) => {
        setSelectedFunction(event.target.value);
    };

    useEffect(() => {
        const fetchFunctions = async () => {
            setIsLoading(true);

            try {
                const bearer_token = localStorage.getItem("bearer_token");
                const endpoint_id = localStorage.getItem("endpoint_id");

                const myHeaders = new Headers();
                myHeaders.append("Content-Type", "application/json");
                myHeaders.append("Authorization", `Bearer ${bearer_token}`);

                const raw = JSON.stringify({
                    company_id: "3a07f396-7af7-49e8-b391-5c414b9ad520",
                    endpoint_id: endpoint_id,
                    format: "excel-manifest",
                });

                const requestOptions = {
                    method: "POST",
                    headers: myHeaders,
                    body: raw,
                };

                const response = await fetch(
                    "https://zpdwekrejwwncdikouve.supabase.co/functions/v1/api-gateway/api/v1/function-list",
                    requestOptions
                );

                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                const result = await response.json();
                console.log(result);

                if (result?.success && Array.isArray(result?.data?.functions)) {
                    setFunctions(result?.data?.functions);
                }
            } catch (error) {
                console.error("Error fetching functions:", error);
            } finally {
                setIsLoading(false);
            }
        };

        fetchFunctions();
    }, []);


    return (
        <>
            {isLoading && <Loader />}
            <FormControl sx={{ m: 1, minWidth: 200 }} size="small">
                <InputLabel id="function-select-label">Function</InputLabel>
                <Select
                    labelId="function-select-label"
                    id="function-select"
                    value={selectedFunction}
                    label="Function"
                    onChange={handleChange}
                >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>

                    {functions.map((fn) => (
                        <MenuItem key={fn.name} value={fn.name}>
                            {fn.name}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
        </>
    );
};

export default Dashboard;