import React, { useState } from "react";
import { Tabs as MuiTabs, Tab, Stack } from "@mui/material";
import { customTheme } from '../../../hooks/customTheme';
import { ThemeProvider } from '@mui/material/styles';
import Header from "../header";
import { useTranslation } from "react-i18next";
import Dashboard from "./dashboard/page";
import LicenseManagement from "./licenseManagement/page";

const TabsPage: React.FC = () => {
    const { t } = useTranslation();
    const [tabIndex, setTabIndex] = useState(0);

    const handleTabChange = (_: React.SyntheticEvent, newIndex: number) => {
        setTabIndex(newIndex);

        const nextTab = newIndex === 0 ? "dashboard" : "licenseManagement";
        console.log("Switched to tab:", nextTab);
    };

    return (
        <>
            <ThemeProvider theme={customTheme}>
                {/* Header */}
                <Header />

                {/* Tabs */}
                <MuiTabs
                    value={tabIndex}
                    onChange={handleTabChange}
                    variant="fullWidth"
                    sx={{ margin: 1 }}
                >
                    <Tab
                        label={t("tabs.dashboard.title")}
                        sx={{
                            textTransform: "capitalize",
                            color: "#292929ff",
                            fontSize: "12px",
                            fontWeight: 400,
                            "&.Mui-selected": { fontWeight: 500 },
                        }}
                    />
                    <Tab
                        label={t("tabs.licenseManagement.title")}
                        sx={{
                            textTransform: "capitalize",
                            color: "#292929ff",
                            fontSize: "12px",
                            fontWeight: 400,
                            "&.Mui-selected": { fontWeight: 500 },
                        }}
                    />
                </MuiTabs>

                <Stack sx={{ p: 2 }}>
                    {tabIndex === 0 && <Dashboard />}
                    {tabIndex === 1 && <LicenseManagement />}
                </Stack>
            </ThemeProvider>


        </>
    );
};

export default TabsPage;