import React, { useState, useEffect } from 'react';
import { Stack, AppBar, Toolbar, IconButton, Tooltip, Typography, MenuItem, Select, SelectChangeEvent, TextField, FormControl, InputLabel } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { customTheme } from '../../../hooks/customTheme';
import { ThemeProvider } from '@mui/material/styles';
import { IoMdArrowRoundBack } from 'react-icons/io';
import { useTranslation } from 'react-i18next';
import UIText from '../../../utilities/textResource';
import { useData } from '../../../hooks/dataContext';

const Setting: React.FC = () => {
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const { selectedLanguage, setSelectedLanguage } = useData();

    useEffect(() => {
        i18n.changeLanguage(selectedLanguage);
    }, [selectedLanguage, i18n]);

    const handleLanguageChange = (event: SelectChangeEvent) => {
        const newLanguage = event.target.value as string;
        setSelectedLanguage(newLanguage);
        i18n.changeLanguage(newLanguage);
        localStorage.setItem('language', newLanguage);
    };

    const handleReturnToHome = () => {
        console.log('Return to home clicked!');

        navigate('/');
    };

    return (
        <>
            <ThemeProvider theme={customTheme}>

                {/* Header */}
                <AppBar sx={{ backgroundColor: '#107C41', position: 'static' }}>
                    <Toolbar sx={{ display: "flex", alignItems: "center", gap: 2 }}>
                        <Tooltip title={t('header.returnToHome')} placement="left-end">
                            <IconButton
                                aria-label={t('header.returnToHome')}
                                size="small"
                                sx={{ fontSize: "20px" }}
                                onClick={handleReturnToHome}
                            >
                                <IoMdArrowRoundBack style={{ color: "#fff" }} />
                            </IconButton>
                        </Tooltip>

                        <Typography sx={{ fontWeight: 600, fontSize: "18px" }}>
                            {t('header.title')}
                        </Typography>
                    </Toolbar>
                </AppBar>

                {/* Language Selector */}
                <Stack sx={{ p: 3, maxWidth: '600px' }}>
                    <FormControl fullWidth>
                        <InputLabel id="langauge-selector">{UIText.setting.selectLanguage}</InputLabel>
                        <Select
                            variant="outlined"
                            size="small"
                            label="Select Language"
                            id="langauge-selector"
                            value={selectedLanguage}
                            onChange={handleLanguageChange}
                            sx={{
                                width: '100%',
                                maxWidth: "600px",
                            }}
                        >
                            <MenuItem value="en">{UIText.setting.english}</MenuItem>
                            <MenuItem value="sp">{UIText.setting.spanish}</MenuItem>
                        </Select>
                    </FormControl>
                </Stack>
            </ThemeProvider>
        </>
    );
};

export default Setting;