export const authenticateUser = async (licenseKey: string, companyID: string, callback: (error: Error | null, data: any | null) => void): Promise<void> => {
    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    const raw = JSON.stringify({
        "license_code": licenseKey,
        "company_id": companyID
    });

    const requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw
    };

    fetch(`${process.env.AUTH_BASE_URL}/functions/v1/auth-endpoint`, requestOptions)
        .then((response) => response.json())
        .then((result) => {
            if (result.success !== true) {
                console.log(result);
                callback(result.error, null);
            } else {
                console.log(result);
                callback(null, result);
            }
        })
        .catch((error) => {
            console.error(error);
            callback(error.error, null);
        });
}