import React, { useState, useEffect } from 'react';
import { Button, Stack, TextField, Typography } from '@mui/material';
import { customTheme } from '../../../hooks/customTheme';
import { ThemeProvider } from '@mui/material/styles';
import { ToastState } from '../../../utilities/types';
import Loader from '../../loader/Loader';
import Toast from '../../Toast/Toast';
import Header from '../header';
import { useTranslation } from 'react-i18next';
import { useData } from '../../../hooks/dataContext';
import { useNavigate } from 'react-router-dom';
import { authenticateUser } from '../../../auth/auth';

const Auth: React.FC = () => {
    const navigate = useNavigate();
    const { t, i18n } = useTranslation();
    const { selectedLanguage } = useData();
    const [toast, setToast] = useState<ToastState>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [licenseKey, setLicenseKey] = useState('');
    const [companyID, setCompanyID] = useState('');
    const [errors, setErrors] = useState({ licenseKey: '', companyID: '' });

    const bearer_token = localStorage.getItem("bearer_token");
    const endpoint_id = localStorage.getItem("endpoint_id");

    useEffect(() => {
        if (selectedLanguage) {
            i18n.changeLanguage(selectedLanguage);
        }

        if (bearer_token && endpoint_id) {
            navigate('/tabs');
        }
    }, [selectedLanguage, i18n]);

    const handleLicenseKeyChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setLicenseKey(value);
        setErrors((prev) => ({
            ...prev,
            licenseKey: value.trim() ? '' : t('auth.errors.licenseKeyRequired'),
        }));
    };

    const handleCompanyIDChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setCompanyID(value);
        setErrors((prev) => ({
            ...prev,
            companyID: value.trim() ? '' : t('auth.errors.companyIdRequired'),
        }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        const licenseKeyError = licenseKey.trim() ? '' : t('auth.errors.licenseKeyRequired');
        const companyIDError = companyID.trim() ? '' : t('auth.errors.companyIdRequired');
        setErrors({ licenseKey: licenseKeyError, companyID: companyIDError });

        if (licenseKeyError || companyIDError) return;

        try {
            setIsLoading(true);
            if (licenseKey && companyID) {
                authenticateUser(licenseKey, companyID, (error, data) => {
                    if (error) {
                        console.log("Authentication failed", error);
                        setToast({ type: 'error', message: `${error}`, time: Date.now() });
                        setIsLoading(false);
                    } else if (data) {
                        console.log("Authentication success", data);

                        // Add licenseKey, companyID into session_info
                        data.data.session_info.license_key = licenseKey;
                        data.data.session_info.company_id = companyID;

                        const { bearer_token, endpoint_id, expires_at } = data.data.session_info;

                        localStorage.setItem('bearer_token', bearer_token);
                        localStorage.setItem('endpoint_id', endpoint_id);
                        localStorage.setItem('expires_at', expires_at);

                        setIsLoading(false);
                        navigate('/tabs');
                    }
                });
            } else {
                throw new Error('Invalid credentials');
            }
        } catch (error) {
            setErrors((prev) => ({
                ...prev,
                companyID: t('auth.errors.invalidCredentials'),
            }));
            setIsLoading(false);
        }
    };

    const isButtonDisabled =
        !!errors.licenseKey || !!errors.companyID || !licenseKey.trim() || !companyID.trim();

    return (
        <>
            {isLoading && <Loader />}
            <Header />

            <ThemeProvider theme={customTheme}>
                <Stack sx={{ minHeight: '100vh', backgroundColor: '#ffffff' }}>
                    <Stack sx={{ margin: '12px', maxWidth: '600px' }}>
                        {/* Page description */}
                        <Typography sx={{ fontSize: '14px' }}>
                            {t('auth.description')}
                        </Typography>

                        {/* Form */}
                        <form onSubmit={handleSubmit}>
                            <Stack spacing={2} mt={2}>
                                {/* License Key */}
                                <TextField
                                    label={t('auth.licenseKeyLabel')}
                                    variant="outlined"
                                    size="small"
                                    value={licenseKey}
                                    onChange={handleLicenseKeyChange}
                                    error={!!errors.licenseKey}
                                    helperText={errors.licenseKey}
                                    sx={{ width: '100%', maxWidth: '600px' }}
                                />

                                {/* Company ID */}
                                <TextField
                                    label={t('auth.companyIdLabel')}
                                    variant="outlined"
                                    size="small"
                                    value={companyID}
                                    onChange={handleCompanyIDChange}
                                    error={!!errors.companyID}
                                    helperText={errors.companyID}
                                    sx={{ width: '100%', maxWidth: '600px' }}
                                />

                                {/* Submit button */}
                                <Button
                                    variant="contained"
                                    type="submit"
                                    disabled={isButtonDisabled}
                                    sx={{
                                        mt: 2,
                                        width: '100%',
                                        color: 'white',
                                        textTransform: 'none',
                                        padding: '8px 42px',
                                        borderRadius: 20,
                                        fontWeight: 600,
                                        fontSize: '0.875rem',
                                        boxShadow: isButtonDisabled ? '' : '0 4px 8px rgba(52, 168, 83, 0.3)',
                                        opacity: isButtonDisabled ? 0.6 : 1,
                                        cursor: isButtonDisabled ? 'not-allowed' : 'pointer',
                                        background: isButtonDisabled ? '#ccc' : '#107C41',
                                        '&:hover': {
                                            bgcolor: isButtonDisabled ? '#ccc' : '#107C41',
                                        },
                                    }}
                                >
                                    {t('auth.button')}
                                </Button>
                            </Stack>
                        </form>
                    </Stack>
                </Stack>

                {/* Toasts */}
                {toast?.type === 'info' && <Toast key={toast.time} info={toast.message} />}
            </ThemeProvider>
        </>
    );
};

export default Auth;